
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Search from 'vux/src/components/search/index.vue'
import CheckIcon from 'vux/src/components/check-icon/index.vue'

import Scroll from "@/components/scrollAll";
export default {
components: {
Flexbox,
FlexboxItem,
XHeader,
Scroll,
Search,
CheckIcon
},
created() {
this.selectId = this.$route.query.userId || 0;
},

data() {
return {
selectId: 0,
clientList: [],
params: {
searchName: "",
pageIndex: 1,
pageSize: 20
},
pullUp: {
threshold: 0,
txt: {
more: "加载更多",
noMore: "没有更多了"
}
},
pullDown: {
threshold: 50,
stop: 20
},
isSort: false
};
},
methods: {
searchUser() {
this.getData(1);
},
onChecked(item) {
this.$bus.emit("userSelected", item);
this.$router.back();
},
getData(type) {
let _url = `${mdcloud}yunos/user/queryUserListByOrg`;

if (type == 1) {
this.params.pageIndex = 1;
}
this.Axios.get(_url, { params: this.params }).then(res => {
let _list = res.data.data.data;

_list.map(item => {


if (item.id == this.selectId) {
item.checked = true;
} else {
item.checked = false;
}
});
if (_list.length > 0) {
if (type === 2) {
this.clientList = [...this.clientList, ..._list];
this.$refs.scroll.scroll.finishPullUp();
} else {
this.clientList = _list;
this.$refs.scroll.scroll.finishPullDown();
}
this.params.pageIndex += 1;
this.$refs.scroll.refresh();
} else {
this.$refs.scroll.forceUpdate();
}
});
}
},
mounted() {
this.getData();
let hei = document.body.clientHeight;
this.$refs.clientList.style.height = `${hei - 88}px`;
},
watch: {
clientList() {
setTimeout(() => {
this.$refs.scroll.forceUpdate(true);
}, 500);
}
}
};



